  methods: {
    demoControlShowCodeTotgal (index) {
      if (this.demoControlShowCode[index]) {
        this.demoControlShowCode[index].show = !this.demoControlShowCode[index].show
      } else {
        this.demoControlShowCode[index] = {
          show: true
        }
      }
    }
  }
}
